import { Builder } from './Builder'

export interface LabelPrice {
    label: string
    amount: number
}

export class LabelPrices extends Builder {

    public declare value: LabelPrice[]
    public constructor(...prices: LabelPrice[]){
        super({ value: prices ?? [ ], parseVal: true })
    }

    public add(label: string, amount: number){
        this.value.push({ label, amount })
    }

}